import React, { useState, useEffect } from "react";

import { BdsInputPassword } from 'blip-ds/dist/blip-ds-react';
import { showToast } from "../../api/commomService"
import "./index.css"


let socket;

const Config = () => {
    const [key, setKey] = useState(localStorage.getItem('plugin-message-attendant-key'));

    const setValueKey = (event) => {

        event.preventDefault();
        setKey(event.detail.value)

    }

    const setValueKeyLocalStorage = () => {

        if (!key || !key.includes("Key")) {
            showMessage('Verifique se digitou a chave corretamente!', 'danger', 'Notice!');
            return false;
        }

        localStorage.setItem("plugin-message-attendant-key", key);
        showMessage('Informação gravada com sucesso!', 'success', 'Notice!');
    }
    const showMessage = (msg, type) => {

        setTimeout(() => {
            showToast({ type: type, message: msg })
        }, 100);

    }


    return (
        <>
            <div className="container w-100">

                <div className="col-7">
                    <label>Chave HTTP do bot de atendimento:</label>
                    <BdsInputPassword label="Digite a chave do seu bot de Atendimento Humano" onBdsInputPasswordChange={(event) => setValueKey(event)} value={key} />
                    <br></br>
                    <button onClick={setValueKeyLocalStorage} className="bp-btn bp-btn--bot bp-btn--large" style={{ width: '100%' }}>Salvar</button>

                </div>
                <br></br>
                <hr />
            </div>

        </>
    )

};

export default Config;
