import React, { useState, useEffect } from "react";
//import queryString from "query-string";
import io from "socket.io-client";
import { BdsInput } from 'blip-ds/dist/blip-ds-react';
import "./Chat.css";

import getQueue from "../../../api/blip/servicePanelQueue";
import getTunnel from "../../../api/blip/servicePanelTunnel";
import uuid from "../../../utils/guid";
import TextContainer from "../TextContainer/TextContainer";
import { showToast } from "../../../api/commomService"

let socket;


const Chat = () => {

  const [name, setName] = useState("Admin");
  const [room, setRoom] = useState(localStorage.getItem("plugin-message-tenantId"));
  const [users, setUsers] = useState([]);
  const [newUsers, setNewUsers] = useState("");
  const [message, setMessage] = useState("");
  const [messageQueue, setMessageQueue] = useState("");

  const ENDPOINT = "https://message-parse.herokuapp.com/";


  useEffect(() => {

    socket = io(ENDPOINT);

    setRoom(room);
    setName("Admim");

    socket.emit("join", { name, room }, (error) => {
      if (error) {
        alert(error);
      }
    });

  }, []);

  useEffect(() => {

    socket.on("roomData", ({ users }) => {
      const result = users.filter((e) => e.name !== 'admin');

      setNewUsers(result);
      setUsers(result);

    });

  }, []);

  const sendMessage = (event) => {
    event.preventDefault();

    if (message) {
      socket.emit("sendMessage", message, () => setMessage(""));
      showMessage('Mensagem enviada com sucesso!', 'success', 'Notice!');
      return false;
    
    }
    showMessage('Verifique se o campo de mensagem esta preenchido!', 'danger', 'Notice!');

  };

  const gravaMensagem = (event) => {
    event.preventDefault();
    setMessage(event.detail.value)
  };
  const showMessage = (msg, type) => {

    setTimeout(() => {
      showToast({ type: type, message: msg })
    }, 100);

  }


  return (
    <>
      <div className="container w-100">
        <div className="row">

          <div className="col-4">
            <label>Contas Ativas:</label>
            <TextContainer users={newUsers} />
          </div>
          <div></div>
          <div className="col-8">
            <label>Mensagem:</label>
            <BdsInput
              label="Passe um recado para seu time:"
              value={message}
              onBdsChange={(event) => gravaMensagem(event)}
              isTextarea="true"
              rows={5}
              onKeyPress={(event) => (event.key === "Enter" ? sendMessage(event) : null)}
            />
            <br></br>
            <button onClick={(e) => sendMessage(e)} className="bp-btn bp-btn--bot bp-btn--large" style={{ width: '100%' }}>Enviar Mensagem</button>
          </div>
          <br></br>
          <hr />
        </div>
      </div>
    </>
  )

};

export default Chat;
